import React, {useState} from 'react';
import {Sidebar, Menu, MenuItem, SubMenu} from 'react-pro-sidebar';
import { NavLink} from "react-router-dom";
import side from "../../assets/img/side.webp";
import side_action from "../../assets/img/side_action.webp";
import side_action_phome from "../../assets/img/side_action_phome.webp";


const ProSideBar = () => {
    //const { collapseSidebar, toggleSidebar, collapsed, toggled, broken, rtl } = useProSidebar();
    const [activeBusiness,setActiveBusiness] = useState(false);
    const [activeEducation,setActiveEducation] = useState(false);

    return (
        <aside className="page__side animate__animated animate__fadeInLeft">
            <Sidebar>
                <Menu className="page-menu menu-page ">
                    <SubMenu active={activeBusiness} className="menu-page__list" onOpenChange={()=>{
                        setActiveBusiness(!activeBusiness)
                    }} routerLink={
                        <NavLink to={"/personal_cabinet"} />
                    } className={`menu-page__link menu-page__link-01 menu-page__link-arrow `}
                             label={" "}>
                        <MenuItem  routerLink={
                            <NavLink to={"my_team"} className="menu-page__sub-link"/>}>
                            </MenuItem>
                        <MenuItem routerLink={<NavLink to={"my_bonus"} className="menu-page__sub-link"/>}>
                            </MenuItem>
                        <MenuItem routerLink={<NavLink to={"my_accounts"} className="menu-page__sub-link"/>}>
                            </MenuItem>
                        <MenuItem routerLink={<NavLink to={"my_orders"} className="menu-page__sub-link"/>}>
                            </MenuItem>
                        <MenuItem routerLink={<NavLink to={"my_dynamic"} className="menu-page__sub-link"/>}>
                            </MenuItem>
                    </SubMenu>
                    <MenuItem className="menu-page__link menu-page__link-02"
                              routerLink={<NavLink to={"/messages"}/>}></MenuItem>
                    <MenuItem className="menu-page__link menu-page__link-03"
                              routerLink={<NavLink to={"/partner_registration"}/>}> /<br/>
                        </MenuItem>
                    <MenuItem className="menu-page__link menu-page__link-04"
                              routerLink={<NavLink to={"/marketing"}/>}>  
                        </MenuItem>
                    <MenuItem className="menu-page__link menu-page__link-05"
                              routerLink={<NavLink to={"/news"}/>}></MenuItem>
                    <MenuItem className="menu-page__link menu-page__link-06"
                              routerLink={<NavLink to={"/company_leaders"}/>}> </MenuItem>
                    <SubMenu onOpenChange={()=>setActiveEducation(!activeEducation)}
                              className="menu-page__list"
                                active={activeEducation}
                                className="menu-page__link menu-page__link-07 menu-page__link-arrow"
                             label={""}>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"/education"}/>}>Main</MenuItem>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"/education_module_1"}/>}> 1 - </MenuItem>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"my_bonus"} className="menu-page__sub-link"/>}> 2 -  </MenuItem>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"my_accounts"} className="menu-page__sub-link"/>}> 3 - </MenuItem>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"my_orders"} className="menu-page__sub-link"/>}> 4 -  </MenuItem>
                        <MenuItem className="menu-page__item" routerLink={<NavLink to={"my_dynamic"} className="menu-page__sub-link"/>}> 5 -  </MenuItem>
                    </SubMenu>
                    <MenuItem className="menu-page__link menu-page__link-08"
                              routerLink={<NavLink to={"/media"} />}></MenuItem>
                    <MenuItem className="menu-page__link menu-page__link-09"
                              routerLink={<NavLink to={"/personal_cabinet_update"} />}> </MenuItem>
                    <button className="menu-page__btn btn-side"></button>
                </Menu>
            </Sidebar>
            <div data-da=".page__content,850.98,1" className="page__body">
                <div className="page__column">
                    <div className="page__bonus bonus-side">
                        <div className="bonus-side__label"> </div>
                        <div className="bonus-side__price">150<span> </span></div>
                        {/*<div className="bonus-side__bg ibg">*/}
                        <div className="bonus-side__bg">
                            <img src={side} alt="action"/></div>
                    </div>
                </div>
                <div className="page__column">
                    <div className="page__action action-side">
                        <div className="action-side__title">Coming
                            soon
                        </div>
                        {/*<div className="action-side__bg ibg">*/}
                        <div className="action-side__bg">
                            <img src={side_action} alt="action"/></div>
                        {/*<div className="action-side__bgphone ibg">*/}
                        <div className="action-side__bgphone">
                            <img src={side_action_phome} alt="action"/></div>
                    </div>
                </div>
            </div>
        </aside>
    );
};

export default ProSideBar;